
.h-container {
  margin: 0 3%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.h-nav-btns-container {
  padding: 40px 0 0 0;
  display: flex;
  justify-content: flex-end;
}

.h-main-icon {
  margin-top: 10px;
  height: 80px;
  width: 80px;
}

.h-main-icon:hover {
  margin-top: 10px;
  height: 80px;
  width: 80px;
}


@media only screen and (max-width: 400px) {

  .h-container {
    margin: 5px 4%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .h-nav-btns-container {
    padding: 0 0 0 0;
  }

  .h-main-icon {
    margin: 0;
    height: 40px;
    width: 40px;
  }

  .h-main-icon:hover {
    margin-top: 10px;
    height: 80px;
    width: 80px;
  }
}
.f-container {
  background-color: #EACABB;
  margin: 0;
  height: 150px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.f-social-icon {
  height: 25px;
  width: 25px;
  margin: 0 10px;
}
.i-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: -99px 0 0;
  height: 500px;
  background-image: url(/static/media/bananatree.7eb99ad0.jpeg);
  background-size: cover;
}

.i-title {
  margin: 99px 0 0;
  padding: 0;
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: 15px;
  color: #fff;
  font-weight: normal;
}

.i-break {
  width: 10%;
  border-bottom: 3px solid rgba(234, 202, 187);
  height: 30px;
}

.i-tagline {
  letter-spacing: 4px;
  color: #fff;
  font-weight: normal;
}

@media only screen and (max-width: 400px) {

  .i-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: baseline;
    padding: 0 5%;
    margin: -60px 0 0;
    height: 300px;
  }

  .i-title {
    font-size: 2rem;
    letter-spacing: 10px;
    margin: 0;
  }

  .i-tagline {
    font-weight: normal;
    font-size: 1rem;
  }
}
#about-me {
  border-top: 15px solid rgba(234, 202, 187);
  display: flex;
  align-items: center;
  padding: 100px 15%;
  justify-content: flex-end;
}

.am-text-container {
  width: 60%;
  text-align: right;
  margin-right: 100px;
}

.am-me-icon {
  border-radius: 50%;
  height: 300px;
  width: 300px;
  background-image: url(/static/media/pic.73c90fa0.jpg);
  background-size: cover;
}

@media only screen and (max-width: 400px) {
  #about-me {
    border-top: 10px solid rgba(234, 202, 187);
    padding: 0px 5% 20px;
  }

  .am-text-container {
    width: 100%;
    text-align: left;
    margin-right: 0;
  }

  .am-title {
    font-size: 1.2rem;
  }

  .am-me-icon {
    display: none;
  }
}
.s-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 50px 5% 80px;
  background:
    linear-gradient(
      rgba(255, 255, 255, .8),
      rgba(255, 255, 255, .8)),
    url(/static/media/paint.80a2858a.jpeg);
  background-size: cover;
}

.s-skills {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(205px, 1fr));
}

.s-skill {
  border-top: 15px solid #eacabb;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px 20px 10px;
}

.s-skill-item {
  margin: 5px 0;
}

.s-title {
  margin: 0 0 20px;
  letter-spacing: 5px;
}

.s-subheading {
  letter-spacing: 5px;
  margin: 20px 0 0;
}

.s-icon {
  width: 80px;
  height: 80px;
  margin: 20px 0 40px;
}

@media only screen and (max-width: 400px) {
  .s-container {
    padding: 40px 5%;
    background:
      linear-gradient(
        rgba(255, 255, 255, .8),
        rgba(255, 255, 255, .8)),
      url(/static/media/paint.80a2858a.jpeg);
    background-size: cover;
  }
  .s-title {
    margin: 0 0 10px;
    letter-spacing: 5px;
    font-size: 1.2rem;
  }
}
.pp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.pp-projects-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  width: 100%;
}

.pp-project-container {
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1%;
}

.pp-title {
  color: #fff;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: normal;
}

.pp-icon {
  width: 4%;
  margin: 5%;
}

.pp-icon:hover {
  cursor: pointer;
}


.p-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}

.p-project-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.p-title {
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 2%;
  letter-spacing: 7px;
}

.p-images {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.p-project-img {
  width: 40%;
  margin: 0 auto;
}

.p-text {
  text-align: center;
  margin: 2% 5%;
}

.p-tech-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.p-tech-icon {
  height: 30px;
  margin: 10px;
}

.p-btns {
  display: flex;
  justify-content: center;
  width: 60%;
}

.p-btn {
  background: rgba(222, 172, 148, .7);
  color: #FFF;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2px;
  transition: .5s;
  width: 30%;
  padding:.7%;
}

.p-btn:hover {
  background: rgba(222, 172, 148);
}

@media only screen and (max-width: 400px) {
  .p-title {
    font-size: 1rem;
  }
  .p-project-img {
    width: 90%;
  }
}
* {
  font-family: 'Raleway', sans-serif;
  color: #5f5f5f;
  overflow-x: hidden;
}

body {
  line-height: 1.4;
  letter-spacing: 1px;
  margin: 0;
}
